import React, { useMemo, useState } from 'react';
import CloseIcon from '@mui/icons-material/Close';
import { Box, FormHelperText, IconButton, Paper, Typography } from '@mui/material';
import useMediaQuery from '@/services/useMediaQuery';
import { ForgotPasswordRequest } from '@/@types';
import webAuth from '@/config/auth0.config';
import { ForgotPasswordForm } from './components/ForgotPasswordForm';

interface ForgotPasswordProps {
  toLogin: () => void;
  onSuccess: (email: string) => void;
}

function ForgotPassword({ toLogin, onSuccess }: ForgotPasswordProps) {
  const [loading, setLoading] = useState(false);
  const { isMobile } = useMediaQuery();

  const [error, setError] = useState<string>();

  const handleChangePassword = (data: ForgotPasswordRequest) => {
    setLoading(true);
    const databaseConnection = 'Username-Password-Authentication';

    try {
      webAuth?.changePassword(
        {
          connection: databaseConnection,
          email: data.email,
        },
        error => {
          setLoading(false);
          if (error) handleError(error);
          else {
            onSuccess(data.email);
          }
        }
      );
    } catch (error) {
      handleError('Something went wrong. Please try again later!');
    } finally {
      setLoading(false);
    }
  };

  const handleError = (error?: auth0.Auth0Error | string) => {
    if (typeof error === 'string') {
      setError(error);
    } else {
      setError(error?.policy || 'Something went wrong. Please try again later!');
    }
  };

  const errorsFormatted = useMemo(() => {
    const errors = error?.split('\n');
    return errors?.map(e => <FormHelperText error>{e}</FormHelperText>) || error;
  }, [error]);

  return (
    <Box display="flex" justifyContent="center" alignItems="center" height="100vh">
      <Paper
        elevation={3}
        sx={{
          borderRadius: isMobile ? 0 : 4,
          width: '100%',
          maxWidth: 496,
          height: isMobile ? '100vh' : 'unset',
          display: isMobile ? 'flex' : 'block',
          flexDirection: 'column',
          gap: isMobile ? 5 : 0,
        }}
      >
        <Box>
          <Box display="flex" justifyContent="center" position="relative" padding={2.5}>
            <Typography>Forgot Password</Typography>
            <Box position="absolute" left={10} top={10}>
              <IconButton
                onClick={toLogin}
                sx={{
                  padding: 0,
                }}
              >
                <CloseIcon />
              </IconButton>
            </Box>
          </Box>
          <Box
            sx={{
              height: 'auto',
              borderRadius: 4,
              display: 'flex',
              justifyContent: 'center',
              mt: 10,
              mb: 11.5,
            }}
          >
            <img
              src="https://raw.githubusercontent.com/viktor1910/login-form/main/public/forgot-password.png"
              alt="login-banner"
            />
          </Box>
          <Box px={10}>
            <Typography align="center" variant="body2">
              Please enter the email address or phone number associated with your account.
            </Typography>
          </Box>
          <Box
            sx={{
              px: isMobile ? 4 : 10,
              pt: isMobile ? 6 : 10,
              pb: 6,
            }}
          >
            {error && <Box pb={4}>{errorsFormatted}</Box>}
            <ForgotPasswordForm onForgotPassword={handleChangePassword} isLoading={loading} />
          </Box>
        </Box>
      </Paper>
    </Box>
  );
}

export default ForgotPassword;
