import React from 'react';
import { Grid, Typography } from '@mui/material';
import { LoadingButton } from '@mui/lab';
import { useForm } from 'react-hook-form';
import TextInputController from '@/components/TextField/TextInputController/TextInputController';
import { ForgotPasswordRequest } from '@/@types';

interface ForgotPasswordFormProps {
  onForgotPassword: (data: ForgotPasswordRequest) => void;
  isLoading: boolean;
}

const ForgotPasswordForm = ({ onForgotPassword, isLoading }: ForgotPasswordFormProps) => {
  const { control, handleSubmit } = useForm<ForgotPasswordRequest>({
    defaultValues: {
      email: '',
    },
  });

  const onSubmit = (data: ForgotPasswordRequest) => {
    onForgotPassword(data);
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <Grid spacing={6} container>
        <Grid item xs={12}>
          <TextInputController
            control={control}
            name="email"
            size="small"
            label="Email"
            required
            fullWidth
            rules={{
              email: true,
            }}
          />
        </Grid>
        <Grid item xs={12}>
          <LoadingButton
            variant="contained"
            sx={{
              height: 48,
            }}
            type="submit"
            fullWidth
            loading={isLoading}
          >
            <Typography variant="body2">Submit</Typography>
          </LoadingButton>
        </Grid>
      </Grid>
    </form>
  );
};

export default ForgotPasswordForm;
