import React, { useState } from 'react';
import { Grid, IconButton, InputAdornment, Typography } from '@mui/material';
import { LoadingButton } from '@mui/lab';
import useMediaQuery from '@/services/useMediaQuery';
import { useForm } from 'react-hook-form';
import TextInputController from '@/components/TextField/TextInputController/TextInputController';
import { RequiredAsterisk } from '@/components/RequiredAsterisk';
import { Visibility, VisibilityOff } from '@mui/icons-material';
import { LoginRequest } from '@/@types';
import { validatePassword } from '@/helpers/ultis';

interface LoginFormProps {
  onLogin: (data: LoginRequest) => void;
  toSignUp: () => void;
  toForgot: () => void;
  isLoading: boolean;
}

const LoginForm = ({ onLogin, toSignUp, isLoading, toForgot }: LoginFormProps) => {
  const { isMobile } = useMediaQuery();

  const [showPassword, setShowPassword] = useState(false);

  const handleTogglePassword = () => {
    setShowPassword(prevShowPassword => !prevShowPassword);
  };

  const { control, handleSubmit } = useForm<LoginRequest>({
    defaultValues: {
      email: '',
      password: '',
    },
  });

  const onSubmit = (data: LoginRequest) => {
    onLogin(data);
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <Grid spacing={6} container>
        <Grid item xs={12}>
          <Typography
            variant={isMobile ? 'h4' : 'h3'}
            sx={{
              mb: 2,
            }}
          >
            {isMobile ? 'Sign in' : 'Sign in with Google'}
          </Typography>
          {isMobile && (
            <Grid container spacing={2}>
              <Grid item>
                <Typography variant="body2">New to PQ?</Typography>
              </Grid>
              <Grid item>
                <Typography
                  variant="body2"
                  color="system.link"
                  sx={{
                    cursor: 'pointer',
                  }}
                  onClick={toSignUp}
                >
                  Sign up
                </Typography>
              </Grid>
            </Grid>
          )}
        </Grid>
        {isMobile && (
          <>
            <Grid item xs={12}>
              <TextInputController
                control={control}
                name="email"
                size="small"
                label="Email"
                required
                fullWidth
                rules={{
                  email: true,
                }}
              />
            </Grid>
            <Grid item xs={12}>
              <div>
                <Grid container justifyContent="space-between" mb={1}>
                  <Grid item>
                    <Typography variant="label2" color="system.sub">
                      Password <RequiredAsterisk />
                    </Typography>
                  </Grid>
                  {isMobile && (
                    <Grid item>
                      <Typography
                        variant="label2"
                        color="system.link"
                        sx={{
                          cursor: 'pointer',
                        }}
                        onClick={toForgot}
                      >
                        Forgot password
                      </Typography>
                    </Grid>
                  )}
                </Grid>
                <TextInputController
                  control={control}
                  name="password"
                  label="Password"
                  hideLabel
                  size="small"
                  required
                  fullWidth
                  type={showPassword ? 'text' : 'password'}
                  InputProps={{
                    endAdornment: (
                      <InputAdornment position="end">
                        <IconButton onClick={handleTogglePassword}>
                          {showPassword ? <Visibility /> : <VisibilityOff />}
                        </IconButton>
                      </InputAdornment>
                    ),
                  }}
                  rules={{
                    validate: validatePassword,
                  }}
                />
              </div>
            </Grid>
          </>
        )}

        <Grid item xs={12}>
          <LoadingButton
            variant="contained"
            sx={{
              height: 48,
            }}
            type="submit"
            fullWidth
            loading={isLoading}
          >
            <Typography variant="body2" fontWeight={500}>
              Sign in
            </Typography>
          </LoadingButton>
        </Grid>
      </Grid>
    </form>
  );
};

export default LoginForm;
