import theme from '@/config/theme.config';
import { Modal, Fade, Box, Typography, Button } from '@mui/material';
import React from 'react';

interface VerifyModalProps {
  open: boolean;
  handleClose: () => void;
  toLogin: () => void;
  des?: 'signUp' | 'forgot';
  email?: string;
}

const style = {
  position: 'absolute' as 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 350,
  bgcolor: 'background.paper',
  borderRadius: 4,
  p: 6,
};

const VerifyModal = ({ open, handleClose, des, email, toLogin }: VerifyModalProps) => {
  return (
    <Modal open={open} onClose={handleClose}>
      <Fade in={open}>
        <Box sx={style}>
          <img
            src="https://raw.githubusercontent.com/viktor1910/login-form/2b3c950d21068f71ac98673daec55eb825cbb820/public/icon-signup-success.svg"
            alt="success-signup"
            style={{
              marginLeft: 'auto',
              marginRight: 'auto',
              display: 'block',
            }}
          />
          <Typography id="transition-modal-title" variant="h5" component="h2" sx={{ my: 4 }} align="center">
            Please verify your email
          </Typography>
          {des === 'forgot' ? (
            <Typography variant="body2" id="transition-modal-description" sx={{ mt: 2, mb: 8 }} align="center">
              We've just sent an email to{' '}
              <Typography component="span" color={theme.palette.system.link}>
                {email}
              </Typography>{' '}
              that you can reset your password.
            </Typography>
          ) : (
            <div>
              <Typography variant="body2" id="transition-modal-description" align="center">
                We have sent an email to{' '}
                <Typography component="span" color={theme.palette.system.link}>
                  {email}
                </Typography>{' '}
                with instructions for verifying your email address.
              </Typography>
              <Typography variant="body2" id="transition-modal-description" sx={{ mt: 4 }} align="center">
                Please check your inbox to verify and continue with the onboarding process.
              </Typography>
              <Typography variant="body2" id="transition-modal-description" sx={{ mt: 4, mb: 8 }} align="center">
                Didn’t get the email? Please contact{' '}
                <Typography component="span" color={theme.palette.system.link}>
                  support@pulsetcm.sg
                </Typography>
              </Typography>
            </div>
          )}

          <Button
            variant="contained"
            sx={{
              height: 48,
            }}
            type="submit"
            fullWidth
            onClick={toLogin}
          >
            <Typography variant="body2">Sign in</Typography>
          </Button>
        </Box>
      </Fade>
    </Modal>
  );
};

export default VerifyModal;
